@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
*{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body{height:100%;margin:0;padding:0}body{font-family:'Lato', 'Helvetica Neue', sans-serif;background-color:#F4F4F7}h1{font-family:'Anton', sans-serif}a.link-unstyled{color:inherit}a.link-unstyled:hover{color:inherit;text-decoration:none}.btn{padding:0.375rem 2rem}.btn.btn-primary{color:#fff;background-color:#7DCD40;border-color:#7DCD40}.btn.btn-primary:not(:disabled):not(.disabled):hover{color:#fff;background-color:#7DCD40;border-color:#7DCD40}.btn.btn-primary:not(:disabled):not(.disabled):active{color:#fff;background-color:#7DCD40;border-color:#7DCD40}.btn.btn-primary:not(:disabled):not(.disabled):focus{box-shadow:0 0 0 0.2rem rgba(125,205,64,0.5)}.btn.btn-danger{color:#fff;background-color:#CD4040;border-color:#CD4040}.btn.btn-danger:not(:disabled):not(.disabled):hover{color:#fff;background-color:#CD4040;border-color:#CD4040}.btn.btn-danger:not(:disabled):not(.disabled):active{color:#fff;background-color:#CD4040;border-color:#CD4040}.btn.btn-danger:not(:disabled):not(.disabled):focus{box-shadow:0 0 0 0.2rem rgba(255,64,64,0.5)}

.Navbar{width:100%;padding:0.2em 1em;background-color:#1C3643}.Navbar__brand{color:#ffffff;display:flex;align-items:center}.Navbar__brand:hover{color:#ffffff;text-decoration:none}.Navbar__brand-logo{margin-right:0.5rem}

.home{padding:2em;min-height:calc(100vh - 60px);display:flex;flex-direction:column;background:url(/curso_de_react.js/static/media/stars.1d17360a.svg),#1B1B25;justify-content:center;align-items:center}.home__message{margin:0 10px;color:white;max-width:400px}.home__message--logo{max-width:400px;width:100%}.home__img{max-width:50%;width:100%;margin-left:50px}@media screen and (min-width: 1200px){.home{padding-left:20%;padding-right:20%;flex-direction:row}.home__message{margin:0 3em;color:white;max-width:40% !important;width:100%;text-align:center}.home__message--logo{max-width:100%;width:100%}.home__img{max-width:60% !important;margin-left:auto !important;margin-top:2em}}@media screen and (min-width: 768px) and (max-width: 1200px){.home{flex-direction:row}.home__img{max-width:700px !important;margin-left:auto !important;margin-top:2em}}@media screen and (max-width: 768px){.home__img{max-width:400px !important;margin-left:0 !important;margin-top:2em}}

.BadgeNew__hero{width:100%;padding:2rem 0;background:url(/curso_de_react.js/static/media/stars.1d17360a.svg),#1b1b25;background-repeat:repeat;margin-bottom:1rem;color:#ffffff;text-align:center}.BadgeNew__hero img{max-width:200px;width:100%}@media screen and (max-width: 768px){.BadgeNew__container{flex-direction:column-reverse}}

.loader{text-align:center;display:flex;justify-content:center;margin-top:3em;margin-bottom:3em}

.Badge{background:#ffffff;box-shadow:0 4px 8px 0 rgba(0,0,0,0.1);border-radius:8px 8px 8px 8px;overflow:hidden;height:100%;max-height:380px;color:black;margin-bottom:2em}.Badge__header{padding:0.5rem 0;height:80px;background:#1b1b25;display:flex;justify-content:center}.Badge__header img{width:110px}.Badge__section-name{display:flex;justify-content:center;align-items:center;padding:1rem 0}.Badge__section-name--avatar{border-radius:50%;margin-right:1rem;width:120px;height:120px}.Badge__section-info{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:0.5rem 0;background:#f4f4f7}.Badge__footer{height:54px;display:flex;justify-content:center;align-items:center;color:#98ca3f;font-weight:bold;font-style:italic}

.form{max-width:300px;margin-left:5em;background-color:white;padding:1em 1em;transform:translateY(-8em);border-radius:20px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.1)}@media screen and (max-width: 768px){.form{transform:translateY(0em) !important;margin:0 auto;margin-bottom:2em}}

.List-element{display:flex;background-color:white;align-items:center;box-shadow:2px 4px 11px -1px rgba(0,0,0,0.75);margin:2em auto;padding:1em 1.5em;border-radius:10px;max-height:100%}.List-element__avatar{width:5em;height:5em;border-radius:40px;margin-right:2em;box-shadow:0px 3px 8px -1px rgba(0,0,0,0.75)}.List-element__info{max-width:20em;width:100%}.List-element__info h3{font-size:22px;margin-bottom:5px}.List-element__info p{font-size:16px;margin:3px}.List-element__info__twitter{color:#00a4d5;font-weight:400}.List-element__info__twitter--logo{width:1.5em;height:1.5em;margin-right:0.5em}.Badge-buttons{display:flex;flex-direction:column}.Badge-buttons__btn{width:2.2em;height:2.2em;text-align:center;padding:.1em;border-radius:50%}.Badge-buttons__btn--img{max-width:1.5em}@media only screen and (max-width: 490px){.List-element{flex-direction:column;text-align:center}.List-element__avatar{margin:1em auto}.Badge-buttons{margin-top:1em;flex-direction:row;max-width:100px;width:100%;justify-content:space-evenly}}

.Badges__hero{width:100%;padding:2rem 0;background:url(/curso_de_react.js/static/media/stars.1d17360a.svg),#1B1B25;background-repeat:repeat;margin-bottom:1rem}.Badges__hero>.Badges__container{display:flex;flex-direction:column;align-items:center}.Badges__container{width:100%;max-width:580px;margin:0 auto;padding:0 1rem}.Badges__logo{margin-bottom:2rem;max-width:250px;width:100%}.Badges__buttons{display:flex;justify-content:flex-end;margin-bottom:1rem}

.PageError{display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:calc(100vh - 60px);width:100%;font-size:7em;color:greenyellow;background-color:#1b1b25}@media screen and (min-width: 470px) and (max-width: 768px){.PageError{font-size:4em !important}}@media screen and (max-width: 470px){.PageError{font-size:3em !important}}

.RickAndMorty-list .List-element{flex-direction:column;text-align:center;min-height:360px;display:flex;background-color:white;align-items:center;box-shadow:2px 4px 11px -1px rgba(0,0,0,0.75);margin:2em auto;padding:1em 1.5em;border-radius:10px}.RickAndMorty-list .List-element__avatar{max-width:11em;max-height:11em;width:100%;height:100%;border-radius:40px;margin:1em auto;box-shadow:0px 3px 8px -1px rgba(0,0,0,0.75)}.RickAndMorty-list .List-element__info h3{font-size:22px;margin-bottom:5px;font-weight:700}.RickAndMorty-list .List-element__info p{font-size:16px;margin:3px}.RickAndMorty-list .List-element__info__twitter{color:#00a4d5;font-weight:400}.RickAndMorty-list .List-element__info__twitter--logo{width:1.5em;height:1.5em;margin-right:0.5em}

.RickAndMorty{background-color:#a9d3e9;padding-bottom:10px}.RickAndMorty__hero{width:100%;padding:2rem 0;background:url(/curso_de_react.js/static/media/stars.1d17360a.svg),#1B1B25;background-repeat:repeat;margin-bottom:1rem}.RickAndMorty__hero>.RickAndMorty__container{display:flex;flex-direction:column;align-items:center}.RickAndMorty__container{width:100%;margin:0 auto;padding:0 1rem}.RickAndMorty__logo{margin-bottom:2rem;max-width:400px;width:100%}

.BadgeEdit__hero{width:100%;padding:2rem 0;background:url(/curso_de_react.js/static/media/stars.1d17360a.svg),#1b1b25;background-repeat:repeat;margin-bottom:1rem;color:#ffffff;text-align:center}.BadgeEdit__hero img{max-width:200px;width:100%}@media screen and (max-width: 768px){.BadgeEdit__container{flex-direction:column-reverse}}

